// @flow weak

import React from 'react';
import {
  Route,
  Switch
 }                              from 'react-router';
import {
  ConnectedHome,
  ConnectedAbout
}                               from '../containers';
import {
  PageNotFound
}                               from '../views';

export const MainRoutes = () => {
  return (
    <Switch>
      <Route exact path="/" component={ConnectedHome} />
      <Route path="/about" component={ConnectedAbout} />
      <Route component={PageNotFound} />
    </Switch>
  );
};

export default MainRoutes;



